.main {
  display: flex;
  justify-content: center;
}

.box {
  max-width: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px;
  box-sizing: border-box;

  img {
    width: 80%;
  }
}

button {
  background-color: cornsilk;
  padding: 10px 20px;
  border-radius: 12px;
  margin: 8px;
  border: 1px solid #ddd699;
  cursor: pointer;

  &:hover {
    background-color: #d6ca83;
    color: white;
  }
}

.tie {
  border: 5px solid gray;
  color: gray;
}
.lose {
  border: 5px solid black;
  color: black;
  // background: fade-out(rgba(#555, 0.5), 0.4);
  filter: opacity(30%);
}
.win {
  border: 10px solid red;
  color: red;
}

.resultP {
  font-size: 50px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
}